/* ScrollToTopButton.css: botón fijo abajo a la derecha */
.scroll-to-top-btn {
  position: fixed;
  right: 32px;
  bottom: 32px;
  z-index: 1000;
  background: none;
  border: none;
  outline: none;
  cursor: pointer;
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.3s;
  box-shadow: 0 4px 16px rgba(61,52,39,0.12);
}
.scroll-to-top-btn.show {
  opacity: 1;
  pointer-events: auto;
}
.scroll-to-top-btn svg {
  display: block;
}
.scroll-to-top-btn:hover svg circle {
  fill: #a68b5b;
}
@media (max-width: 600px) {
  .scroll-to-top-btn {
    right: 16px;
    bottom: 16px;
  }
  .scroll-to-top-btn svg {
    width: 24px;
    height: 24px;
  }
}
