
.projects-page-bg {
  min-height: 100vh;
  width: 100vw;
  background: linear-gradient(135deg, #f5f1eb 0%, #e8ddd4 30%, #d4c4b0 70%, #c4b49d 100%);
  background-attachment: fixed;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 80px 0 60px 0;
  position: relative;
  overflow: hidden;
}

.projects-page-bg::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: radial-gradient(circle at 20% 80%, rgba(139, 115, 85, 0.1) 0%, transparent 50%),
              radial-gradient(circle at 80% 20%, rgba(166, 139, 91, 0.1) 0%, transparent 50%);
  pointer-events: none;
}

.projects-title {
  font-size: 3.5rem;
  font-weight: 300;
  color: #3d3427;
  margin-bottom: 3rem;
  letter-spacing: 0.02em;
  text-align: center;
  font-family: 'Helvetica Neue', Arial, sans-serif;
  position: relative;
  z-index: 1;
}

.projects-title::after {
  content: '';
  position: absolute;
  bottom: -15px;
  left: 50%;
  transform: translateX(-50%);
  width: 100px;
  height: 4px;
  background: linear-gradient(to right, #8b7355, #a68b5b, #8b7355);
  border-radius: 2px;
}

.projects-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 2.5rem;
  width: 90vw;
  max-width: 1200px;
}

.project-card {
  background: linear-gradient(145deg, #ffffff 0%, #fefcfa 100%);
  border-radius: 24px;
  box-shadow: 0 10px 40px rgba(61,52,39,0.12), 
              0 4px 20px rgba(139, 115, 85, 0.08);
  overflow: hidden;
  display: flex;
  flex-direction: column;
  transition: all 0.4s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  border: 1px solid rgba(139, 115, 85, 0.1);
  position: relative;
  backdrop-filter: blur(10px);
}

.project-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 3px;
  background: linear-gradient(to right, #8b7355, #a68b5b, #c4b49d);
  transform: scaleX(0);
  transition: transform 0.4s ease;
}

.project-card:hover::before {
  transform: scaleX(1);
}

.project-card:hover {
  transform: translateY(-12px) scale(1.02);
  box-shadow: 0 20px 60px rgba(61,52,39,0.18), 
              0 8px 30px rgba(139, 115, 85, 0.12);
}

.project-image {
  width: 100%;
  height: 200px;
  object-fit: cover;
  border-top-left-radius: 24px;
  border-top-right-radius: 24px;
  transition: transform 0.4s ease;
  filter: brightness(0.95) saturate(1.1);
}

.project-card:hover .project-image {
  transform: scale(1.05);
  filter: brightness(1) saturate(1.2);
}

.project-content {
  padding: 1.5rem 1.2rem 1.2rem 1.2rem;
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.project-title {
  font-size: 1.4rem;
  font-weight: 600;
  color: #3d3427;
  margin-bottom: 0.8rem;
  font-family: 'Helvetica Neue', Arial, sans-serif;
  line-height: 1.3;
  transition: color 0.3s ease;
}

.project-card:hover .project-title {
  color: #8b7355;
}

.project-description {
  font-size: 1rem;
  color: #6d5c43;
  margin-bottom: 1.2rem;
  flex: 1;
}

.learn-more {
  display: inline-block;
  background: linear-gradient(135deg, #8b7355 0%, #a68b5b 100%);
  color: #ffffff;
  padding: 0.8rem 1.6rem;
  border-radius: 25px;
  font-size: 0.95rem;
  font-weight: 600;
  text-decoration: none;
  transition: all 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  box-shadow: 0 4px 15px rgba(139, 115, 85, 0.3);
  margin-top: 0.8rem;
  text-align: center;
  position: relative;
  overflow: hidden;
}

.learn-more::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, #a68b5b 0%, #c4b49d 100%);
  transition: left 0.3s ease;
  z-index: -1;
}

.learn-more:hover::before {
  left: 0;
}

.learn-more:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(139, 115, 85, 0.4);
  color: #3d3427;
}

@media (max-width: 900px) {
  .projects-grid {
    grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
    gap: 1.5rem;
  }
  .project-image {
    height: 140px;
  }
}

@media (max-width: 600px) {
  .projects-title {
    font-size: 2rem;
    margin-bottom: 1.2rem;
  }
  .projects-page-bg {
    padding: 30px 0 20px 0;
  }
}

/* Technologies Section */
.technologies-section {
  margin-top: 4rem;
  margin-bottom: 2rem;
  width: 90vw;
  max-width: 1200px;
  position: relative;
  z-index: 1;
}

.technologies-title {
  font-size: 2.5rem;
  font-weight: 300;
  color: #3d3427;
  text-align: center;
  margin-bottom: 2.5rem;
  letter-spacing: 0.02em;
  font-family: 'Helvetica Neue', Arial, sans-serif;
  position: relative;
}

.technologies-title::after {
  content: '';
  position: absolute;
  bottom: -10px;
  left: 50%;
  transform: translateX(-50%);
  width: 80px;
  height: 3px;
  background: linear-gradient(to right, #8b7355, #a68b5b, #8b7355);
  border-radius: 2px;
}

.technologies-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(180px, 1fr));
  gap: 1.5rem;
  margin-top: 1rem;
}

.tech-card {
  background: linear-gradient(145deg, #ffffff 0%, #fefcfa 100%);
  border-radius: 16px;
  padding: 1.5rem 1rem;
  text-align: center;
  box-shadow: 0 8px 25px rgba(61,52,39,0.08), 
              0 3px 15px rgba(139, 115, 85, 0.05);
  border: 1px solid rgba(139, 115, 85, 0.1);
  transition: all 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  position: relative;
  overflow: hidden;
}

.tech-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 2px;
  background: linear-gradient(to right, #8b7355, #a68b5b);
  transform: scaleX(0);
  transition: transform 0.3s ease;
}

.tech-card:hover::before {
  transform: scaleX(1);
}

.tech-card:hover {
  transform: translateY(-8px) scale(1.02);
  box-shadow: 0 15px 40px rgba(61,52,39,0.12), 
              0 6px 20px rgba(139, 115, 85, 0.08);
}

.tech-icon {
  font-size: 2.5rem;
  display: block;
  margin-bottom: 0.8rem;
  filter: grayscale(0.2);
  transition: all 0.3s ease;
}

.tech-card:hover .tech-icon {
  transform: scale(1.1);
  filter: grayscale(0);
}

.tech-name {
  display: block;
  font-size: 1.1rem;
  font-weight: 600;
  color: #3d3427;
  margin-bottom: 0.5rem;
  transition: color 0.3s ease;
}

.tech-card:hover .tech-name {
  color: #8b7355;
}

.tech-category {
  display: block;
  font-size: 0.85rem;
  color: #a68b5b;
  font-weight: 400;
  text-transform: uppercase;
  letter-spacing: 0.05em;
}
